import React, { Component } from 'react'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import Paper from '@material-ui/core/Paper'
import TextField from '@material-ui/core/TextField'
import NativeSelect from '@material-ui/core/NativeSelect'
import Button from '@material-ui/core/Button'
import FormControl from '@material-ui/core/FormControl'
import InputLabel from '@material-ui/core/InputLabel'
import CircularProgress from '@material-ui/core/CircularProgress'
import { post } from 'axios'

import outward from './json/outward'
import getBookingInfo from './json/getBookingInfo'
import Trains from './Trains'
import Result from './Result'
import Utils from './Utils'

const styles = {
    content: {
        margin: 24,
        padding: 24,
    },
    control: {
        marginRight: 12,
        minWidth: 120,
    },
    loading: {
        width: 48,
        height: 48,
        marginTop: 24,
        marginLeft: 'calc(50% - 48px)',
    }
};

const cities = [
    { code: 'FRPAR', label: 'Paris' },
    { code: 'FRLYS', label: 'Lyon' },
];

const times = [...Array(24).keys() ];

class App extends Component {
    state = {
        date: '',
        card: '',
        birth: '',
        time: '',
        origin: '',
        destination: '',
        seat: 'ANY',
        deck: 'ANY',
        trains: [],
        fares: [],
        query: {},
        stations: {},
        selected: [],
        loading: false,
        analysis: false,
        result: null,
    };

    componentDidMount = () => {
        let date = new Date();
        date.setDate(date.getDate() + 30);

        this.setState({
            time: 18,
            date: Utils.formatDate(date),
            card: localStorage.getItem('card') || '',
            birth: localStorage.getItem('birth') || '',
            deck: localStorage.getItem('deck') || 'ANY',
            seat: localStorage.getItem('seat') || 'ANY',
            origin: 'FRLYS',
            destination: 'FRPAR',
        });
    };

    handleChange = (field) => (event) => {
        this.setState({ [field]: event.target.value });

        if (field === 'card' || field === 'birth' || field === 'seat' || field === 'deck') {
            localStorage.setItem(field, event.target.value);
        }
    };

    searchTrains = () => {
        this.refreshTrains();
        this.setState({ loading: true, selected: [] });
    };

    refreshTrains = (cyclicAnalysis) => {
        const departure = Utils.formatDateTime(this.state.date, this.state.time);
        const birthDate = Utils.formatDateTime(this.state.birth, 0);
        const query = outward(this.state.origin, this.state.destination, departure, this.state.card, birthDate);
        post('https://www.oui.sncf/proposition/rest/search-travels/outward', query).then(response => {
            this.setState({
                query: query,
                trains: response.data.trainProposals,
                fares: response.data.fareInformations,
                stations: response.data.detailedLocalities,
                loading: false,
            });

            if (this.state.selected.length > 0) {
                let trains = {};
                for (let train of response.data.trainProposals) {
                    trains[train.id] = train;
                }

                this.setState({ selected: this.state.selected.map(train => trains[train.id]) });
            }

            if (cyclicAnalysis && this.state.analysis) {
                this.cyclicAnalysis();
            }
        });
    };

    searchTGVMaxPrice = (train) => {
        for (let price of train.priceProposals) {
            if (price.amount === 0) {
                return price;
            }
        }

        return null;
    };

    searchTGVMaxTrain = () => {
        let priceTGVMax;
        for (let train of this.state.selected) {
            priceTGVMax = this.searchTGVMaxPrice(train);

            if (priceTGVMax) {
                return train;
            }
        }

        return null;
    };

    toggleTrain = (train) => {
        const index = this.state.selected.indexOf(train);
        let selected = this.state.selected;

        if (index >= 0) {
            selected.splice(index, 1);
        } else {
            selected.push(train);
        }

        this.setState({ selected });
    };

    bookTrain = (train) => {
        const price = this.searchTGVMaxPrice(train);
        const input = getBookingInfo(train, price, this.state.fares, this.state.query, this.state.deck, this.state.seat);

        post('https://www.oui.sncf/proposition/rest/get-booking-info', input).then(response => {
            const body = document.getElementsByTagName('body')[0];
            let form = document.createElement('form');
            form.setAttribute('method', 'post');
            form.setAttribute('action', response.data.url);

            for (let key in response.data.parameters) {
                let field = document.createElement('input');
                field.setAttribute('type', 'hidden');
                field.setAttribute('name', key);
                field.setAttribute('value', response.data.parameters[key]);
                form.appendChild(field);
            }

            body.appendChild(form);
            form.submit();
        });

        this.setState({ selected: [], trains: [], analysis: false, result: train });
    };

    cyclicAnalysis = () => {
        const train = this.searchTGVMaxTrain();
        if (train) {
            this.bookTrain(train);
        } else {
            setTimeout(() => this.refreshTrains(true), 5000);
        }
    };

    toggleAnalysis = () => {
        this.setState({ analysis: !this.state.analysis });

        if (!this.state.analysis) {
            this.cyclicAnalysis();
        }
    };

    render() {
        return (
            <div>
                <AppBar position="static" color="primary">
                    <Toolbar>
                        <Typography variant="title" color="inherit">
                            TGVMax Booking
                        </Typography>
                    </Toolbar>
                </AppBar>
                <Paper style={styles.content}>
                    <TextField
                        label="Code TGVMax"
                        placeholder="HC..."
                        InputLabelProps={{ shrink: true }}
                        onChange={this.handleChange('card')}
                        value={this.state.card}
                        style={styles.control}
                        disabled={this.state.analysis}
                    />
                    <TextField
                        label="Date de naissance"
                        type="date"
                        InputLabelProps={{ shrink: true }}
                        onChange={this.handleChange('birth')}
                        value={this.state.birth}
                        style={styles.control}
                        disabled={this.state.analysis}
                    />
                    <FormControl
                        style={styles.control}
                        disabled={this.state.analysis}
                    >
                        <InputLabel>Salle par défaut</InputLabel>
                        <NativeSelect
                            value={this.state.deck}
                            onChange={this.handleChange('deck')}
                            disabled={this.state.analysis}
                        >
                            <option value="ANY">Indifférent</option>
                            <option value="HAUT">Haute</option>
                            <option value="BAS">Basse</option>
                        </NativeSelect>
                    </FormControl>
                    <FormControl
                        style={styles.control}
                        disabled={this.state.analysis}
                    >
                        <InputLabel>Place par défaut</InputLabel>
                        <NativeSelect
                            value={this.state.seat}
                            onChange={this.handleChange('seat')}
                            disabled={this.state.analysis}
                        >
                            <option value="ANY">Indifférent</option>
                            <option value="FEN">Fenêtre</option>
                            <option value="COU">Couloir</option>
                            <option value="DUO_COTE_A_COTE">Duo côte à côte</option>
                        </NativeSelect>
                    </FormControl>
                    <br />
                    <br />

                    <FormControl
                        style={styles.control}
                        disabled={this.state.analysis}
                    >
                        <InputLabel>Depuis</InputLabel>
                        <NativeSelect
                            value={this.state.origin}
                            onChange={this.handleChange('origin')}
                            disabled={this.state.analysis}
                        >
                            {cities.map(city => <option key={city.code} value={city.code}>{city.label}</option>)}
                        </NativeSelect>
                    </FormControl>
                    <FormControl
                        style={styles.control}
                        disabled={this.state.analysis}
                    >
                        <InputLabel>Vers</InputLabel>
                        <NativeSelect
                            value={this.state.destination}
                            onChange={this.handleChange('destination')}
                        >
                            {cities.map(city => <option key={city.code} value={city.code}>{city.label}</option>)}
                        </NativeSelect>
                    </FormControl>
                    <TextField
                        label="Date"
                        type="date"
                        InputLabelProps={{ shrink: true }}
                        onChange={this.handleChange('date')}
                        value={this.state.date}
                        style={styles.control}
                        disabled={this.state.analysis}
                    />
                    <FormControl
                        style={styles.control}
                        disabled={this.state.analysis}
                    >
                        <InputLabel>Créneau</InputLabel>
                        <NativeSelect
                            value={this.state.time}
                            onChange={this.handleChange('time')}
                        >
                            {times.map(time => <option value={time} key={time}>{time}h</option>)}
                        </NativeSelect>
                    </FormControl>
                    <br />
                    <br />

                    <Button
                        variant="contained" 
                        color="primary"
                        style={styles.control}
                        disabled={this.state.loading || this.state.analysis || !this.state.card || !this.state.birth}
                        onClick={this.searchTrains}
                    >Rechercher les trains</Button>
                    <Button
                        variant="contained"
                        color="secondary"
                        disabled={this.state.selected.length <= 0}
                        style={styles.control}
                        onClick={this.toggleAnalysis}
                    >{this.state.analysis ? 'Stopper' : 'Lancer'} l'analyse</Button>
                </Paper>

                {!this.state.loading && <Trains
                    analysis={this.state.analysis}
                    trains={this.state.trains}
                    stations={this.state.stations}
                    selected={this.state.selected}
                    toggleTrain={this.toggleTrain}
                />}

                {this.state.loading || this.state.analysis ? 
                    <CircularProgress
                        style={styles.loading}
                        color={this.state.loading ? 'primary' : 'secondary'}
                    /> : 
                    null
                }

                {this.state.result && <Result
                    train={this.state.result}
                    stations={this.state.stations}
                />}
            </div>
        );
    }
}

export default App



